.Background {
  background-image: url("../../../assets/images/background.png");
  height: 500px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  position: relative;
  margin-top: 72px;
}

.Text {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  height: 74%;
  max-width: 40%;
  transform: translate(-50%, -50%);
}

section {
  margin: auto;
  align-content: center;
}

.Icons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 90%;
}

.Button {
  display: flex;
  justify-content: space-around;
}

.Why {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Cards {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: auto;
}

.About {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: #ffa500;
  margin: 4% 0;
}

.aboutImage {
  width: 60%;
  margin: auto;
  outline: none;
  padding-bottom: 2%;
}

.Press {
  color: #ffa500;
}

.jumboText {
  width: 100%;
}

.btnGroup {
  margin: 30px auto;
  text-align: center;
}

.btnGroup button {
  margin: 0 10px;
}

@media (min-width: 1198px) {
  .jumboText {
    width: 40%;
  }
}
